
window.onscroll = function () {
    scrollFunction()
};

function scrollFunction() {
    if (document.body.scrollTop > 20 || document.documentElement.scrollTop > 20) {
        document.getElementById("navbar").style.top = "-135px";
    } else {
        document.getElementById("navbar").style.top = "0px";
    }
}

function startTime() {
    var today = new Date();
    var h = today.getHours();
    var m = today.getMinutes();
    var s = today.getSeconds();
    h = checkTime(h);
    m = checkTime(m);
    s = checkTime(s);
    var t = setTimeout(startTime, 500);
    var day;
    switch (today.getDay()) {
        case 0: day = "Sunday"; break;
        case 1: day = "Monday"; break;
        case 2: day = "Tuesday"; break;
        case 3: day = "Wednesday"; break;
        case 4: day = "Thursday"; break;
        case 5: day = "Friday"; break;
        case 6: day = "Saturday"; break;
    }
    var month;
    switch (today.getMonth()) {
        case 0: month = "January"; break;
        case 1: month = "February"; break;
        case 2: month = "March"; break;
        case 3: month = "April"; break;
        case 4: month = "May"; break;
        case 5: month = "June"; break;
        case 6: month = "July"; break;
        case 7: month = "August"; break;
        case 8: month = "September"; break;
        case 9: month = "October"; break;
        case 10: month = "November"; break;
        case 11: month = "December"; break;
    }
    var date = today.getDate();
    var pow;
    if(date==11||date==12||date==13){
        pow = "st"
    }else{
    switch (date % 10) {
        case 1:
            pow = "st";
            break;
        case 2:
            pow = "nd";
            break;
        case 3:
            pow = "rd";
            break;
        default:
            pow = "th";
            break;
    }
}
    var y = today.getFullYear();
    document.getElementById('txt').innerHTML = h + ":" + m + ":" + s + "\n" + day + "\t" + date +pow.sup()+ "\t" + month + "\t" + y;

    
    // var d = new Data();
    document.fgColor = "black";
    var x = today.getHours();
    if (x < 12) document.getElementById('hello').innerHTML = "Good Morning";
    else if (x >= 12&&x<18) document.getElementById('hello').innerHTML = "Good Afternoon";
    else if (x >= 18) document.getElementById('hello').innerHTML = "Good Evening";
}
function checkTime(i) {
    if (i < 10) { i = "0" + i };  // add zero in front of numbers < 10
    return i;
}


var slideIndex = 0;
showSlides();
function showSlides() {
    var i;
    var slides = document.getElementsByClassName("mySlides");
    var dots = document.getElementsByClassName("dot");
    for (i = 0; i < slides.length; i++) {
        slides[i].style.display = "none";
    }
    slideIndex++;
    if (slideIndex > slides.length || slideIndex < 1) {
        slideIndex = 1
    }
    for (i = 0; i < dots.length; i++) {
        dots[i].className = dots[i].className.replace(" active", "");
    }
    slides[slideIndex - 1].style.display = "block";
    dots[slideIndex - 1].className += " active";
    setTimeout(showSlides, 3000); // Change image every 3 seconds
}

